
img {
  image-rendering: crisp-edges;
}

.sale-view {
  position: absolute!important;
  margin-top: 0!important;
  top: 0!important;
  right: 0!important;
  z-index: 1!important;
  padding: 8px!important;
  padding-left: 10px!important;
  padding-right: 10px!important;
  text-shadow: rgba(109, 109, 109, 0.521569) 1px 1px 2px!important;
  -webkit-box-shadow: -1px 2px 20px!important;
          box-shadow: -1px 2px 20px!important;
  background: red;
  color: #fff;
}

.sale-view i, .sale-view .sale-text {
  vertical-align: middle;
  zoom: 0.1;
}

.product-view span.card-title {
  text-shadow: rgba(0, 0, 0, 0.4) 1px 1px 3px, rgba(255, 255, 255, 0.33) -1px 1px 2px;
  vertical-align: middle;
  background-color: rgba(0,0,0,0.2);
  padding-top: 2px;
  padding-bottom: 2px;
  width: 100%;
  padding-right: 46px;
  padding-right: 60px!important;
  zoom: 0.9;
}

.product-view span.card-title span.title {
  min-width: 100%!important;
}

.product-view .card-content {
  overflow-y: auto;
  min-height: 100px;
  max-height: 100px;
}

.product-view .card-image {
  min-height: 204px!important;
}

.product-view img {
  min-height: 204px!important;
}

@media screen and (max-width: 1124px) {
  .product-view span.card-title {
    zoom: 0.7;
  }
}

@media screen and (max-width: 988px) {
  .product-view span.card-title {
    zoom: 0.8;
  }
}

@media screen and (max-width: 616px) {
  .product-view span.card-title {
    zoom: 1;
  }
}



